import React from "react";
import Axios from "axios";
import { useState, useEffect } from "react";
import "../scss/CountryList.scss";
import _ from "lodash";

const CountryList = (props) => {
  const [countryList, setCountryList] = useState(null);
  // const [currentButton, setCurrentButton] = useState(1);
  const [currentPage, setCurrentPage] = useState();
  const url = "https://corona.lmao.ninja/v2/countries?yesterday&sort";
  let content = null;
  const contentPerPage = 24;

  async function getCountryList() {
    try {
      const response = await Axios.get(url);
      // console.log(response.data);
      setCountryList(response.data);
      //     setPagination(response.data.slice(0, 4));
      setCurrentPage(1);
    } catch (error) {
      console.error(error);
    }
  }
  useEffect(() => {
    getCountryList();
  }, [url]);

  const pageCount = countryList ? Math.ceil(764 / countryList.length) : 0;

  const pageNumbers = countryList ? Math.ceil(222 / contentPerPage) : 0;
  if (pageCount === 1) {
    return null;
  }

  const page = _.range(1, pageNumbers + 1);
  // const postPerPage = countryList.length;
  //console.log(postPerPage);

  if (countryList) {
    const IndexOFLast = currentPage * contentPerPage;
    // console.log(IndexOFLast, currentPage);
    const indexOfFirst = IndexOFLast - contentPerPage;
    const currentData = countryList.slice(indexOfFirst, IndexOFLast);

    // console.log(currentPage, clickedPage);

    content = currentData
      // .filter((country, index) => {
      //   return country.deaths > 70000;
      // })
      .map((country, index) => (
        <div className="container-1" key={index}>
          <div className="card">
            <div className="index-number">
              {index + 1 < 10 ? <h2>0{index + 1}</h2> : <h2>{index + 1}</h2>}
            </div>
            <div className="country-name">
              <h2> {country.country}</h2>
            </div>
            <div className="country-flag">
              <img src={country.countryInfo.flag}></img>
            </div>
            <div className="un-ordered-list">
              <ul>
                <li>Population: {country.population}</li>
                <li>Cases: {country.cases}</li>
                <li>Recovered: {country.recovered}</li>
                <li>Deaths: {country.deaths}</li>
              </ul>
            </div>
          </div>
        </div>
      ));
  }

  return (
    <div className="country">
      {content}
      <div className="pagination">
        <ul>
          {page.map((pages) => (
            <li
              key={pages}
              onClick={() => setCurrentPage(pages)}
              className={currentPage === pages ? "active" : "disabled"}
            >
              {pages}
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default CountryList;
